import React, { Component } from 'react';
import './App.css';
import { BsThreeDots } from "react-icons/bs";
import { FaMapPin } from "react-icons/fa";
import tr1 from "./images/1.jpg";
import tr2 from "./images/1.png"
import tr3 from "./images/3.png";
import tr4 from "./images/3.jpg"
import tr5 from "./images/5.jpg";
import tr6 from "./images/6.png"
import tr7 from "./images/8.png";
import tr8 from "./images/7.jfif"

let iconDots = <BsThreeDots />;
let img1 = <img src={tr1} height="80px" width="80px"/>
let img2 = <img src={tr2} height="80px" width="80px"/>
let img3 = <img src={tr3} height="80px" width="80px"/>
let img4 = <img src={tr4} height="80px" width="80px"/>
let img5 = <img src={tr5} height="80px" width="80px"/>
let img6 = <img src={tr6} height="80px" width="80px"/>
let img7 = <img src={tr7} height="80px" width="80px"/>
let img8 = <img src={tr8} height="80px" width="80px"/>
let btnMore = <button className="btnMore">View More</button>

class Table extends Component{
    
    constructor(props) {
        super(props)
        this.state = {
            table: [
               {logo: img1, company: "Tech Solutions", training: 'React JS', level: 'beginner', location: "Yerevan"},
               {logo: img2, company: "ICLT", training: 'English Lessons for IT', level: 'middle', location: "Yerevan"},
               {logo: img3, company: "SFL", training: 'QA trainings', level: 'beginner', location: "Vanadzor"},
               {logo: img4, company: "SoftCode", training: 'Scrum / Agile', level: 'beginner', location: "Stepanakert"},
               {logo: img5, company: "Menu.am", training: 'JAVA', level: 'middle', location: "Gyumri"},
               {logo: img6, company: "Krisp", training: 'UI/UX Design', level: 'beginner', location: "Yerevan"},
               {logo: img7, company: "Altacode", training: 'HTML/CSS trainings', level: 'beginner', location: "Kapan"},
               {logo: img8, company: "Picsart", training: 'Big Data', level: 'middle', location: "Yerevan"},
               {logo: img1, company: "Tech Solutions", training: 'React JS', level: 'beginner', location: "Yerevan"},
               {logo: img2, company: "ICLT", training: 'English Lessons for IT', level: 'middle', location: "Yerevan"},
               {logo: img3, company: "SFL", training: 'QA trainings', level: 'beginner', location: "Vanadzor"},
               {logo: img4, company: "SoftCode", training: 'Scrum / Agile', level: 'beginner', location: "Stepanakert"}
 
            ]
        }
        
    }
  
    renderTableData() {
        return this.state.table.map((tableItem, index) => {
           const { logo, company, training, level, location, btn } = tableItem;
           return (
              <tr >
                 {/* <td>{id}</td> */}
                 <td>{logo}</td>
                 <td>{company}</td>
                 <td>{training}</td>
                 <td>{level}</td>
                 <td><FaMapPin /> {location}</td>
                 <td>{btnMore}</td>
                 {/* <td>{change}</td> */}
                 {/* <td>{price}</td>
                 <td>{priceBTC}</td>
                 <td>{marketCap}</td>
                 <td>{volume }</td>
                 <td>{icon}</td> */}
              </tr>
           )
        })
     }

     renderTableHeader() {
        let header = Object.keys(this.state.table[0])
        return header.map((key, index) => {
           return <th key={index}>{key.toUpperCase()}</th>
        })
     }

    render() { 
        return (
           <div>
              <table id='table'>
                <tbody>
                  <tr>{this.renderTableHeader()}</tr>
                  {this.renderTableData()}
               </tbody>
            </table>
           </div>
        )
     }
  }





export default Table;