import img from "./images/10.jpg";
import { FaMapPin } from "react-icons/fa";
import { BiTime } from "react-icons/bi";


function Adjob() {
    return (
        <div>
            <span style={{color:"dimgray"}}>Jobs recommended for you</span>
            <hr></hr>
          <div className="adJob">
            <img src={img} height="130px" width="130px"/>
           <div className="description">
             <p>IT Support Admin</p>
             <FaMapPin /> <span>Yerevan</span>
             <p> <BiTime />2021-July-10</p>
           </div>

          </div>
        </div>
    )
}
export default Adjob;